<template>
  <div id="projectMiniBox"
       :class="{ bigProject : showingBig }"
       @click="showingBig = !showingBig">
    <h3>{{ project.name }}</h3>
    <p>{{ project.description }}</p>
    <a v-if="showingBig"
       :href="project.url"
       target="_blank"
       class="button">Check it out on GitHub</a>
  </div>
</template>

<script>
export default {
  name: 'projectMini',
  props: ['project'],
  data () {
    return {
      showingBig: false
    }
  }
}
</script>

<style scoped>
  .bigProject{
    width: 100%;
  }
#projectMiniBox{
  width: 40%;
  padding: 5%;
  float: left;
  display: block;
}
.button{
  padding: 1em;
  background-color: #673AB7;
  color: white;
  border-radius: 10px;
  display: block;
  text-decoration: none;
  transition: 0.5s;
}
.button:hover{
  background-color: #7E57C2;
  color: white;
  border-radius: 15px;
}
#projectMiniBox:hover{
  background-color: darkgray;
  cursor: pointer;
}
@media screen and (max-width: 700px) {
  #projectMiniBox{
    width: 100%;
    padding: 2% 0;
  }
}
</style>
